import React, { PureComponent } from "react";
import { elementHoc } from "../../libs/atoms/ElementHoc";
import { css } from "@emotion/react";
import * as Styles from "../../libs/styles";
const style = (props) => {
	const newprops = { ...props, children: null, className: null };
	return css`
		${Styles.Mq.moreTab} {
			width: 24vw;
		}
		width: 65vw;
		height: auto;
		margin-top: 0%;
		fill: #000;
		${{ ...newprops }}
	`;
};
export class Logo02 extends PureComponent {
	render() {
		let {
			className,
			forwardedRef,
			children,
			data,
			caption = null,
			...rest
		} = this.props;

		const stylesObj = {
			display: "block",
		};

		return (
			<svg
				ref={forwardedRef}
				css={data.func(stylesObj, data.instance)}
				className={`${className ? className : ""} `}
				{...rest}
				viewBox="0 0 252 36"
				preserveAspectRatio="xMinYMin meet"
			>
				<use xlinkHref="#bosa-logo" x="0" y="0" />
			</svg>
		);
	}
}

export default elementHoc(Logo02);
