import React, { Component } from "react";

import { css } from "@emotion/react";

import * as Common from "../../common";

import * as Libs from "libs";

class Footer extends Component {
	//--------------------------------------
	//  Libs.Styless
	//--------------------------------------
	containerCss = () => {
		//background-color: #f0F;
		return css`
			background-color: #fff;
			height: auto;
			width: 100vw;

			position: relative;
			z-index: 20;
			.on-story & {
				display: none;
			}
		`;
	};

	descItemCont = () => css`
		${Libs.Styles.Mq.moreTab} {
			${Libs.Styles.Position.ChildSpacing("50px")}
		}
		${Libs.Styles.Mq.lessPab} {
			${Libs.Styles.Position.ChildSpacing("30px")}
			margin-top: 30px;
		}
	`;

	subNavContCss = () => {
		return css`
			${Libs.Common.Func.getPcSpVwValue("margin-top", 30, 50)};
			opacity: 1;
			${Libs.Styles.Mq.moreTab} {
				${Libs.Styles.Flex.flexLine({ between: true })}
				width: 80vw;
				margin-left: auto;
				margin-right: auto;
			}
			${Libs.Styles.Mq.lessPab} {
				${Libs.Styles.Position.ChildSpacing("40px")}
				padding-left: 10vw;
			}
		`;
	};

	//--------------------------------------
	//  Scripts
	//--------------------------------------
	constructor(props) {
		super(props);
		this.container = React.createRef();
		this.mount = false;
		const { dispatch } = this.props;
		this.dispatch = dispatch;
		this.langKey = Libs.Common.Func.getLangKey();
	}
	mount = false;
	componentDidMount() {
		this.mount = true;
	}

	render() {
		this.navItemRefs = [];
		const navItems = Common.Config.getNavItems();
		const langKey = Libs.Common.Func.getLangKey();
		const h3Styl = {
			fontPc: {
				fontSize: 14,
				lineHeight: 14,
			},
			fontSp: {
				fontSize: 14,
				lineHeight: 14,
			},
			positionPc: { space: "0 0 7 0" },
			positionSp: { space: "0 0 15 0" },
			whiteSpace: "nowrwap",
		};
		const pStyles = {
			fontPc: {
				fontFace: "sansserif",
				fontSize: 25,
				lineHeight: 30,
				letterSpacing: -0.4,
			},
			fontSp: {
				fontFace: "sansserif",
				fontSize: 25,
				lineHeight: 30,
				letterSpacing: -0.4,
			},
		};

		return (
			<nav css={this.containerCss} ref={this.container} className="footer">
				<div
					css={this.subNavContCss}
					ref={(el) => {
						if (el) this.navItemRefs.push(el);
					}}
				>
					<div css={this.descItemCont}>
						<div>
							<Libs.Atoms.H3 styles={h3Styl}>Site Map</Libs.Atoms.H3>
							<ul>
								{navItems.map((item, i) => (
									<li key={`navItem${i}`}>
										<Libs.Atoms.Span data-hover={item.label} styles={pStyles}>
											<Libs.Atoms.TriggerLink
												transition={
													Libs.Transition.TrigerPageTransition
														.TrigerPageTransition
												}
												to={
													item.to !== "/visit"
														? `/${langKey}${item.to}`
														: `${item.to}`
												}
											>
												{item.label}
											</Libs.Atoms.TriggerLink>
										</Libs.Atoms.Span>
									</li>
								))}
								<li
									css={css`
										margin-top: 14px;
									`}
								>
									<Libs.Atoms.Span
										data-hover={"shopping-info"}
										styles={{
											fontPc: {
												fontFace: "sansserif",
												fontSize: 20,
												lineHeight: 20,
												letterSpacing: -0.4,
											},
											fontSp: {
												fontFace: "sansserif",
												fontSize: 20,
												lineHeight: 20,
												letterSpacing: -0.4,
											},
										}}
									>
										<Libs.Atoms.TriggerLink
											transition={
												Libs.Transition.TrigerPageTransition
													.TrigerPageTransition
											}
											to={`/${langKey}/shopping-info`}
										>
											Shopping Info
										</Libs.Atoms.TriggerLink>
									</Libs.Atoms.Span>
								</li>
								<li>
									<Libs.Atoms.Span
										data-hover={"privacy-policy"}
										styles={{
											fontPc: {
												fontFace: "sansserif",
												fontSize: 20,
												lineHeight: 20,
												letterSpacing: -0.4,
											},
											fontSp: {
												fontFace: "sansserif",
												fontSize: 20,
												lineHeight: 20,
												letterSpacing: -0.4,
											},
										}}
									>
										<Libs.Atoms.TriggerLink
											transition={
												Libs.Transition.TrigerPageTransition
													.TrigerPageTransition
											}
											to={`/${langKey}/privacy-policy`}
										>
											Privacy Policy
										</Libs.Atoms.TriggerLink>
									</Libs.Atoms.Span>
								</li>
							</ul>
						</div>
					</div>
				</div>
			</nav>
		);
	}
}
export default Footer;
